.fn-@{cms-ns}-ad-title{
		text-align: center;

		p{
			font-size: @font-size-base;
			color: @theme-color;
			font-weight: bold;
			padding: 10.5*@rex 0;
			line-height: 1;
			display: inline-block;
			position: relative;

			&:before{
				.size(54*@rex,100%);
				.absolute(0,_,_,-64*@rex);
				content: "";
				display: block;
				background: data-uri('@{cms-world-image-path}/ad-title-left.png') center no-repeat;
				background-size: contain;
			}

			&:after{
				.size(47*@rex,100%);
				.absolute(0,-64*@rex,_,_);
				content: "";
				display: block;
				background: data-uri('@{cms-world-image-path}/ad-title-right.png') center no-repeat;
				background-size: contain;

			}
		}
	}
