@import "../_world-variables.less";
@import "../_world-mixins.less";


.fn-@{cms-ns}-hot-chat{
	background: #fff;

	.img-box{

		&:before{
			padding-top: 380%/7.5;
		}
	}

	.chat-info{
		padding: 3.2% 10%/3.75;

		p{
			.ellipsis(2);
			font-size: @font-size-base;
			color: #666;
		}
	}

	.btn-box{
		text-align: center;
		padding-bottom: 10px;

		.launch-btn{
			display: inline-block;
			color: @theme-color;
			border: 1px solid @theme-color;
			border-radius: 20px;
			padding: 5px 12px;
			font-weight: bold;

			span{
				margin-left: 5px;
			}
		}
	}
}
