@import "../_world-variables.less";
@import "../_world-mixins.less";


.fn-@{cms-ns}-beauty-slide{
	background: #fff;
	padding: 10px 0 0;


	.slide-item{
		padding-left: 5px;
		width: 100%/2.586;

		.item-info{
			p{
				.ellipsis();
				padding: 14px 5px;
				line-height: 1;
				font-size: @font-size-sm;
			}
		}
	}

		.item-more{

			.img-box{
				background: #fff;
				border: 1px solid @theme-color;

				.img{
					text-align: center;

					p:first-child{
						color: @theme-color;
						font-size: @font-size-base;
					}

					p:last-child{
						font-size: @font-size-lg;
					}
				}
			}
		}
}
