@import "../_world-variables.less";
@import "../_world-mixins.less";

.fn-@{cms-ns}-state-nav{
	background: #fff;
	overflow: hidden;
	padding-bottom: 4%;

	.box{
		width: 25%;
		padding-top: 4%;
		float: left;
		text-align: center;
		position: relative;

		.img-box{
			width: 320%/3.75;
			margin: 0 auto;
			border-radius: 50%;
			overflow: hidden;
		}

		.img-info{
			.absolute(_,0,0,_);
			display: block;
			width: 100%;
			line-height: 1;
			padding: 5px 0;
			font-size: @font-size-sm;
			text-align: center;
			color: #fff;
		}


		&:nth-child(1) span{
			background: #fa7023;
		}

		&:nth-child(2) span{
			background: #f92e65;
		}

		&:nth-child(3) span{
			background: #8b55fb;
		}

		&:nth-child(4) span{
			background: #3db8fa;
		}

		&:nth-child(5) span{
			background: #f7af0d;
		}

		&:nth-child(6) span{
			background: #5dbc48;
		}

		&:nth-child(7) span{
			background: #fa331e;
		}

		&:nth-child(8) span{
			background: #fe2d9a;
		}
	}
}
