@import "../_world-variables.less";
@import "../_world-mixins.less";

.fn-@{cms-ns}-bottom-link{
	background: #fff;

	.link{
		text-align: center;
		padding: 15*@rex 0;

		a{
			border: 1px solid #999;
			color: #666;
			padding: 8/2*@rex 45/2*@rex;
			border-radius: 20*@rex;
			display: inline-block;

			&:first-child{
				margin-right: 10%;
			}

			i{
				.size(15*@rex,15*@rex);
				position: relative;
				top: 2.5*@rex;
				display: inline-block;
				background: data-uri("@{cms-world-image-path}/earphone.png") center no-repeat;
				background-size: contain;
			}

			&:last-child i{
					background-image: data-uri("@{cms-world-image-path}/pen.png") ;
				}

			span{
				margin-left: 5*@rex;
			}
		}
	}
}
