@import "../_world-variables.less";
@import "../_world-mixins.less";

.fn-@{cms-ns}-bottom-nav{

	&:before{
		content: "";
		display: block;
		padding-top: 50*@rex;
	}

	>div{
		background: @theme-color;
		text-align: center;
		position: fixed;
		bottom: 0;
		width: 100%;
		z-index: 900;
		max-width: @container-max-width;

	}

	a{
		color: #fff;
		font-size: 10*@rex;

		span{
			.size(100%,22*@rex);
			margin-top: 8*@rex;
			display: block;
			background-position: center;
			background-repeat:  no-repeat;
			background-size: contain;
		}

		p{
			color: #ba84ab;
			line-height: 20*@rex;
		}

		.icon-active~p{
			color: #fff;
		}

		&:nth-child(1) .icon-normal{
			background-image: data-uri('@{cms-world-image-path}/bottom-icon01.png') ;
		}

		&:nth-child(2) .icon-normal{
			background-image: data-uri('@{cms-world-image-path}/bottom-icon02.png') ;
		}

		&:nth-child(3) .icon-normal{
			background-image: data-uri('@{cms-world-image-path}/bottom-icon03.png') ;
		}

		&:nth-child(4) .icon-normal{
			background-image: data-uri('@{cms-world-image-path}/bottom-icon04.png') ;
		}

		&:nth-child(1) .icon-active{
			background-image: data-uri('@{cms-world-image-path}/bottom-icon01-active.png') ;
		}

		&:nth-child(2) .icon-active{
			background-image: data-uri('@{cms-world-image-path}/bottom-icon02-active.png') ;
		}

		&:nth-child(3) .icon-active{
			background-image: data-uri('@{cms-world-image-path}/bottom-icon03-active.png') ;
		}

		&:nth-child(4) .icon-active{
			background-image: data-uri('@{cms-world-image-path}/bottom-icon04-active.png') ;
		}
	}
}
