@import "../_world-variables.less";
@import "../_world-mixins.less";

.fn-@{cms-ns}-timing-buy{
	background: #fff;

	.time-tab{
		a{
			text-align: center;
			font-size: @font-size-base;
			padding: 29/2*@rex 0 25/2*@rex;
			line-height: 1;
			box-sizing: border-box;
			font-weight: bolder;
			border-bottom:2px solid transparent;
		}

		.active{
			color: @price-color;
			border-color: @price-color;
			position: relative;

			&:before{
				position: absolute;
				left: 50%;
				bottom: -8px;
				margin-left: -4px;
				content: "";
				z-index: 100;
				border: 4px solid @price-color;
				border-color: @price-color transparent transparent transparent;
			}
		}
	}

	.time-tip{
		background: @bg-color;

		p{
			float: left;
			line-height: 1;
			padding: 9*@rex 0;
			font-size: @font-size-sm;

			&:last-child{
				float:right;
				padding: 8*@rex 0;

				.count-down{
					font-size: @font-size-base;
				}

				i{
					border:1px solid #d5d5d5;
				}
			}
		}
	}

	.time-buy{
		background: @bg-color;

		.time-item{
			padding-right: 5px;
			width: 100%/3.5714;

			.item-info{
				padding: 10px 5px;
				background: #fff;

				p:first-child{
					.ellipsis(2);
					margin-bottom: 9px;
					font-size: @font-size-sm;
					height: @font-size-lg*2;
				}

				p:last-child{
					font-weight: bold;
					height: 22*@rex;


					.hunt-logo{
						background: @price-color;
						color: #fff;
						float: right;
						font-size: @font-size-sm;
						padding: 4px;
						line-height: 1;
						border-radius: 2px;
						font-weight: normal;
					}
				}
			}
		}
	}
}

