@import "../_world-variables.less";
@import "../_world-mixins.less";

.fn-@{cms-ns}-big-ads{
	margin: 10*@rex 0 0;
	width: 100%;

	.img-box{
		&:before{
			padding-top: 460%/7.5;
		}
	}

	.big-ad{
		height: 100%;

		.ad-arrowhead{
			position: absolute;
			left: 50%;
			bottom: 0;
			content:"";
			display: block;
			margin-left: -10*@rex;
			border-width: 10*@rex;
			border-style: dashed dashed solid dashed;
			border-color: transparent transparent #fff transparent;
			z-index: 100;
		}
	}

}
