@import "../_world-variables.less";
@import "../_world-mixins.less";


.fn-@{cms-ns}-baby-slide{
	background: #fff;

	.time-buy{
		background: #fff!important;

		.item-more{

			.img-box{
				background: #fff;
				border: 1px solid @theme-color;

				.img{
					text-align: center;

					p:first-child{
						color: @theme-color;
						font-size: @font-size-base;
					}

					p:last-child{
						font-size: @font-size-lg;
					}
				}
			}
		}
	}

	.baby-wrapper{
		background: #fff;

		.time-item{
			padding: 0 5px;
			width: 100%/3.409;
		}

		.item-info{
			padding: 10px 0!important;

		}

	}


}
