@import "../_world-variables.less";
@import "../_world-mixins.less";

.fn-@{cms-ns}-main-menu{
	margin: 10*@rex 0;

	.menu-item{
		width: 50%;

		.img-box{
			&:before{
				padding-top: 48.13%;
			}
		}

		&:nth-child(2n){
			float: right;
			padding-left: 0.5px;
		}

		&:nth-child(2n+1){
			float: left;
			padding-right: 0.5px;
		}

		&:nth-child(n+3){
			margin-top: 1px;
		}
	}
}
