@import "../_world-variables.less";
@import "../_world-mixins.less";


.fn-@{cms-ns}-world-nav-new{
	position: relative;
	background: #fff;

	.swiper-container{
		a{
			float: left;
			width: 100%/5.5;
			text-align: center;
			font-size: @font-size-sm;
			line-height: 27*@rex;

			&:before{
				height: 20*@rex;
				margin-top: 7*@rex;
				content: "";
				display: block;
				background-position: bottom center;
				background-repeat:  no-repeat;
				background-size: contain;
			}

			span{
				line-height: 27*@rex;
				display: inline-block;
				border-bottom: 2*@rex solid #fff;
			}
		}

		.icon-normal{
			&:nth-child(6n+1):before{
				background-image: data-uri('@{cms-world-image-path}/nav-icon01.png') ;
			}

			&:nth-child(6n+2):before{
				background-image: data-uri('@{cms-world-image-path}/nav-icon02.png') ;
			}

			&:nth-child(6n+3):before{
				background-image: data-uri('@{cms-world-image-path}/nav-icon03.png') ;
			}

			&:nth-child(6n+4):before{
				background-image: data-uri('@{cms-world-image-path}/nav-icon04.png') ;
			}

			&:nth-child(6n+5):before{
				background-image: data-uri('@{cms-world-image-path}/nav-icon05.png') ;
			}

			&:nth-child(6n):before{
				background-image: data-uri('@{cms-world-image-path}/nav-icon06.png') ;
			}
		}

		.icon-active{
			color: @theme-color;

			span{
				border-bottom: 2px solid @theme-color;
			}

			&:nth-child(6n+1):before{
				background-image: data-uri('@{cms-world-image-path}/nav-icon01-active.png') ;
			}

			&:nth-child(6n+2):before{
				background-image: data-uri('@{cms-world-image-path}/nav-icon02-active.png') ;
			}

			&:nth-child(6n+3):before{
				background-image: data-uri('@{cms-world-image-path}/nav-icon03-active.png') ;
			}

			&:nth-child(6n+4):before{
				background-image: data-uri('@{cms-world-image-path}/nav-icon04-active.png') ;
			}

			&:nth-child(6n+5):before{
				background-image: data-uri('@{cms-world-image-path}/nav-icon05-active.png') ;
			}

			&:nth-child(6n):before{
				background-image: data-uri('@{cms-world-image-path}/nav-icon06-active.png') ;
			}
		}
	}
	.nav-menu{
		.absolute(0,0,_,_);
		width: 56%/3.75;
		height: 100% ;
		overflow: hidden;
		border-left: 1px solid @border-color-base;
		background: rgba(255,255,255,0.8);
		z-index: 99;
		text-align: center;
		font-size: 25*@rex;

		a{
			.size(25px,18px);
			display: block;
			background: data-uri('@{cms-world-image-path}/world-menu.png') center no-repeat;
			background-size: contain;
		}
	}
}


