@import "../_world-variables.less";
@import "../_world-mixins.less";

.fn-@{cms-ns}-personal-recommend{
	background: #fff;

	.img-box{
		width: 100%;

		.sale-logo{
			transform: scale(0.5,0.5);
			.absolute(_,_,0.5rem,0.5rem);
			z-index: 100;

		}

	}

	.info-box{
		padding: 12*@rex;

		.info-tip{
			color: #999;
			font-size: @font-size-sm;

			em{
				.size(16*@rex,11*@rex);
				display: inline-block;
				margin:0rem 5*@rex;
				margin-left: 0px;
				background: data-uri('@{cms-world-image-path}/flag.png') no-repeat center;
			}

			span{
				float: right;

				i{
					color: #8c8c8c;
					font-weight: bold;
				}
			}
		}

		.fn-price-lg{
			font-weight: bold;
		}

		h5{
			.ellipsis(2);
			font-size: @font-size-base;
			font-weight: bolder;
			margin: 5*@rex 0;
		}

		.info-p{
			.ellipsis(4);
			font-size: @font-size-sm;
			color: #666;

		}

	}
}
